import React from "react";
import { observer } from "mobx-react-lite";
import useStore from "../../../hooks/use-store.hook";

import Input from "../../layout/input";
import TextArea from "../../layout/textarea";
import Radio from "../../layout/radio/radio.component";
import Button from "../../layout/button/button.component";

import { IProduct } from "../../../interfaces/product.interface";

import {
  Wrapper,
  Description,
  Title,
  TextAreaWrapper,
  Buttons,
  Form,
  Secret,
} from "./card-body.styles";

type TProps = {
  body: "desc" | "form1" | "form2";
  product: IProduct;
};

type TContactInfo = {
  name: string;
  email: string;
  phone: string;
  message: string;
  adress: string;
  date: string;
  prefer: string;
};

const CardBody = ({ body, product }: TProps) => {
  const store = useStore();
  const { name, phone, email, message, prefer } = store.mailerStore;

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    const { value, name } = e.target;
    store.mailerStore.setValue(name, value);
  };

  const handleTextAreaChange = (
    e: React.ChangeEvent<HTMLTextAreaElement>
  ): void => {
    const { value, name } = e.target;
    store.mailerStore.setValue(name, value);
  };

  const handleSubmit = (e: React.FormEvent): void => {
    e.preventDefault();

    store.mailerStore.sendMessage();
  };

  switch (body) {
    case "desc":
      return (
        <Wrapper center>
          <Title>{product.title}</Title>
          <Description>{product.description}</Description>
        </Wrapper>
      );
    case "form1":
      return (
        <Form onSubmit={handleSubmit}>
          <Input
            value={name}
            onChange={handleChange}
            required
            placeholder="Имя"
            name="name"
          />
          <Input
            value={phone}
            onChange={handleChange}
            required
            placeholder="+7(999)999-99-99"
            name="phone"
          />
          <Input
            value={email}
            onChange={handleChange}
            required
            placeholder="Email"
            name="email"
          />
          <Input
            onChange={handleChange}
            placeholder="Дата и время мероприятия"
            name="date"
          />
          <TextAreaWrapper height={176}>
            <TextArea
              onChange={handleTextAreaChange}
              placeholder="Адрес мероприятия"
              name="adress"
            />
          </TextAreaWrapper>
          <Secret>
            <h4>Конфиденциальность</h4>
            <span>
              Мы обязуемся не передавать полученную информацию третьим лицам
            </span>
          </Secret>
          <Buttons>
            <Button type="submit">
              {store.mailerStore.isMailSended ? "....." : "Отправить"}
            </Button>
            <Radio
              label="Написать"
              handleSelectChange={handleChange}
              checked={prefer === "email"}
              value="email"
              name="prefer"
            />
            <Radio
              label="Позвонить"
              handleSelectChange={handleChange}
              checked={prefer === "phone"}
              value="phone"
              name="prefer"
            />
          </Buttons>
        </Form>
      );
    case "form2":
      return (
        <Form onSubmit={handleSubmit}>
          <Input
            value={name}
            onChange={handleChange}
            required
            placeholder="Имя"
            name="name"
          />
          <Input
            value={phone}
            onChange={handleChange}
            required
            placeholder="+7(999)999-99-99"
            name="phone"
          />
          <Input
            value={email}
            onChange={handleChange}
            required
            placeholder="Email"
            name="email"
          />
          <TextAreaWrapper height={245}>
            <TextArea
              value={message}
              onChange={handleTextAreaChange}
              placeholder="Ваш вопрос по данному аттракциону"
              name="message"
            />
          </TextAreaWrapper>
          <Secret>
            <h4>Конфиденциальность</h4>
            <span>
              Мы обязуемся не передавать полученную информацию третьим лицам
            </span>
          </Secret>
          <Buttons>
            <Button type="submit">
              {store.mailerStore.isMailSended ? "....." : "Отправить"}
            </Button>
            <Radio
              label="Написать"
              handleSelectChange={handleChange}
              checked={prefer === "email"}
              value="email"
              name="prefer"
            />
            <Radio
              label="Позвонить"
              handleSelectChange={handleChange}
              checked={prefer === "phone"}
              value="phone"
              name="prefer"
            />
          </Buttons>
        </Form>
      );
  }
};

export default observer(CardBody);
