import React from "react";
import { observer } from "mobx-react-lite";

import useStore from "../../../hooks/use-store.hook";

import Block from "../../layout/block/block.component";
import Button from "../../layout/button/button.component";
import Input from "../../layout/input";
import TextArea from "../../layout/textarea";
import Radio from "../../layout/radio/radio.component";

import contactsIcon from "../../../resources/icons/contacts.svg";
import secretIcon from "../../../resources/icons/secret.svg";

import {
  Form,
  Icon,
  Title,
  Inputs,
  SubTitle,
  RadioWrapper,
  TextAreaWrapper,
  SecretWrapper,
} from "./contacts.styles";

const Contacts = () => {
  const { mailerStore } = useStore();

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    const { value, name } = e.target;
    mailerStore.setValue(name, value);
  };

  const handleTextAreaChange = (
    e: React.ChangeEvent<HTMLTextAreaElement>
  ): void => {
    const { value, name } = e.target;
    mailerStore.setValue(name, value);
  };

  const handleSubmit = (e: React.FormEvent): void => {
    e.preventDefault();

    mailerStore.sendMessage();
  };

  return (
    <React.Fragment>
      <Icon src={contactsIcon} />
      <Title>Появились вопросы</Title>
      <Form onSubmit={handleSubmit}>
        <Block gridArea="Inputs">
          <Inputs>
            <SubTitle>Обратная связь</SubTitle>
            <span>Мы обязательно свяжемся с вами</span>
            <Input
              placeholder="Имя"
              name="name"
              value={mailerStore.name}
              onChange={handleInputChange}
            />
            <Input
              placeholder="+7(999)999-99-99"
              name="phone"
              value={mailerStore.phone}
              onChange={handleInputChange}
            />
            <Input
              placeholder="Email"
              name="email"
              value={mailerStore.email}
              onChange={handleInputChange}
            />
            <RadioWrapper>
              <Radio
                label="Написать"
                handleSelectChange={handleInputChange}
                checked={mailerStore.prefer === "email"}
                value="email"
                name="prefer"
              />
              <Radio
                label="Позвонить"
                handleSelectChange={handleInputChange}
                checked={mailerStore.prefer === "phone"}
                value="phone"
                name="prefer"
              />
            </RadioWrapper>
            <Button>Задать вопрос</Button>
          </Inputs>
        </Block>
        <Block gridArea="TextArea">
          <TextAreaWrapper>
            <TextArea
              placeholder="Ваш вопрос или комментарий"
              value={mailerStore.message}
              onChange={handleTextAreaChange}
              name="message"
            />
          </TextAreaWrapper>
          <SecretWrapper>
            <Icon src={secretIcon} />
            <span>
              Конфиденциальность
              <p>
                Мы обязуемся не передавать полученную информацию и контактные
                данные третьим лицам.
              </p>
            </span>
          </SecretWrapper>
        </Block>
      </Form>
    </React.Fragment>
  );
};

export default observer(Contacts);
